.backdrop-book {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .216);
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  transition: opacity .5s cubic-bezier(.4, 0, .2, 1), visibility .5s cubic-bezier(.4, 0, .2, 1), transform .5s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop-book.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: scale(1.2);
}

.modal-book {
  width: 100%;
  height: 100%;
  max-width: 335px;
  max-height: 762px;
  background-color: var(--bcg-white-main);
  border: 2px solid var(--bcg-black);
  z-index: 1;
  border-radius: 18px;
  padding: 40px 24px;
  transition: transform .5s;
  position: relative;
  overflow-x: hidden;
  overflow-y: auto;
  transform: scale(.8);
}

