.side-bar {
    display: flex;
    flex-direction: column;
    max-width: 335px;
    gap: 40px;
    padding-top: 40px;
}
  
    @media screen and (min-width: 768px) {
      .side-bar {
      max-width: 672px;
      align-items: center;
      // gap: 32px;
      flex-direction: row;
      
      }
    }
    @media screen and (min-width: 1440px) {
      .side-bar {
      max-width: 356px;
      flex-direction: column;
      gap: 86px;
      }
    }
  
  
  .main {
    display: flex;
    flex-direction: row;
  }